import styled from 'styled-components'

const SiteHeader = styled.header`
  display: flex;
  color: #eee;
  border-bottom: 5px solid #222831;
  padding: 1em;
  margin-bottom: 1em;

    @media (max-width: 600px){
      align-items: center;
      flex-direction: column;
    }
`

const HeaderContent = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin: 0 auto;
  width: 960px;

`

const Logo = styled.img`
  width: 75px;
  height: auto;
  margin; 5px
`

const SiteNav = styled.nav`
  display: flex;
  justify-content: flex-end;
  > ul {
    width: 250px;
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    > li {
      margin: 0px;
      > a {
        padding: 3px;
        &:hover {
          background: #0092CA;
        }
      }
    }
  }
`

export {
  SiteHeader,
  HeaderContent,
  Logo,
  SiteNav}



