import styled from 'styled-components'

const Footer = styled.footer`
  background: #222831;
  height: 120px;
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;

  @media (max-width: 600px){
    justify-content: center;
  }
`

const FooterContent = styled.div`
  margin-right: 15%;

  @media (max-width: 600px){
    margin-right: 0px;
  }
`

const FooterNav = styled.nav`
  > ul {
    list-style: none;
    display: flex;
  }
`

export {
  Footer,
  FooterContent,
  FooterNav
}



