import React from 'react'
import Link from 'gatsby-link'
import PropTypes from 'prop-types'
import Helmet from 'react-helmet'
import logo from '../data/images/CS-logo.svg'
import { SiteHeader, HeaderContent, Logo, SiteNav } from '../components/Header'
import { Footer, FooterContent, FooterNav } from '../components/Footer'
import {GitHubLogo, LinkedInLogo} from '../components/Icons'
import {SiteContainer, ContentWrapper} from '../components/Content'
import './flexstyles.css'

const TemplateWrapper = ({children}) => (
  <div>
    <Helmet title="Chris Scouras - Front End Web Developer" meta={[
      {
        name: 'description',
        content: 'Chris Scouras\'s Front End Web Portfolio'
      }, {
        name: 'keywords',
        content: 'portfolio, web development, front end'
      }
    ]}/>
  <SiteContainer>
      <SiteHeader>
        <HeaderContent>
          <Link to="/">
            <Logo src={logo} alt="logo"></Logo>
          </Link>
          <SiteNav>
            <ul>
              <li>
                <Link to="/projects">Projects</Link>
              </li>
              <li>
                <Link to="/about">About</Link>
              </li>
              <li>
                <Link to="/contact">Contact</Link>
              </li>
            </ul>
          </SiteNav>
        </HeaderContent>
      </SiteHeader>

      <ContentWrapper>
        {children()}
      </ContentWrapper>
      <Footer>
        <FooterContent>
          <FooterNav>
            <ul>
              <a href="https://github.com/cscouras" target="_blank" rel="noopener noreferrer">
                <li><GitHubLogo /></li>
              </a>
              <a href="https://www.linkedin.com/in/cscouras/" target="_blank" rel="noopener noreferrer">
                <li><LinkedInLogo /></li>
              </a>
            </ul>
          </FooterNav>
        </FooterContent>
      </Footer>
    </SiteContainer>
  </div>
)

TemplateWrapper.propTypes = {
  children: PropTypes.func
}

export default TemplateWrapper



